@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;400;600&display=swap);
body, html, #root {
  margin: 0;
  padding: 0;
  position: absolute;
  top: -20px;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Raleway',
    'Josefin Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-page {
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    background-image: url(https://rocketcodearg.github.io/rocket-course-react-project/static/media/home-page-image.444c0615.png);
    background-color: #ffffff;
}
.content-container {
    width: 950px;
    margin: 0 auto;
    vertical-align: middle;
    position: relative;
    top: 185px;
}

.content-container--title {
    padding: 0 170px;
}
.landing-title {
    font-family: 'Raleway', sans-serif;
    text-align: center;
    margin-bottom: 20px;
}

.landing-text {
    text-align: center;
    padding: 0 100px;
    font-family: 'Josefin Sans';
}
.lading-button {
    color: #ffffff;
    background-color: #00e6c9;
    width: 170px;
    text-align: center;
    margin: 0 auto;
    padding: 10px 14px;
    font-size: 18px;
    letter-spacing: 1.8px;
    cursor: pointer;
}

.lading-button:hover {
    background-color: #0aa893;
}
.landing-modal {
    position: fixed;
    margin: 0 auto;
    top: 25%;
    background-color: #ffffff;
    z-index: 10;
    width: 710px;
    height: 446px;
    left: 50%;
    margin-left: calc(-710px/2);
    padding: 70px 77px 60px 77px;
    box-sizing: border-box;
}

.landing-modal--overlay {
    z-index: 1;
    width: 100%;
    height: 100vh;
    background-color: rgba(216,216,216, 0.5);
    position: fixed;
    top: 0;
    left: 0;
}

.landing-modal .landing-title {
    margin-bottom: 38px;
}

.landing-modal .landing-text {
    font-size: 24px;
    padding: 0;
    margin-bottom: 48px;
}

.landing-modal .lading-button {
    margin: 0;
    width: 100%;
    box-sizing: border-box;
}

.landing-modal--close-icon{
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 20px 20px;
    width: 20px;
    height: 20px;
    position: absolute;
    top: 32px;
    right: 32px;
    cursor: pointer;
}
.landing-input-text {
    width: 100%;
    box-sizing: border-box;
    padding: 12px 11px;
    border: 0;
    border-bottom: 4px solid #212121;
    font-size: 18px;
    letter-spacing: 2px;
    outline: none;
}

.landing-input-text--error {
    border-bottom: 4px solid #FF1E02;
}

.landing-input-wrapper {
    height: 124px;
}

.landing-input-feedback {
    font-size: 18px;
    letter-spacing: 2px;
    font-family: 'Josefin Sans';
    padding: 0 11px;
}

.landing-input-text::-webkit-input-placeholder {
    color: #a2a2a2;
}

.landing-input-text:-ms-input-placeholder {
    color: #a2a2a2;
}

.landing-input-text::-ms-input-placeholder {
    color: #a2a2a2;
}

.landing-input-text::placeholder {
    color: #a2a2a2;
}
.landing-tooltip {
    display: flex;
    flex-direction: row;
    border: 1px solid #8e8e8e;
    width: 215px;
    text-align: center;
    padding: 6px 11px;
    box-sizing: border-box;
    position: relative;
    border-radius: 5px;
    margin-top: 12px;
    margin-left: 40px;
}

.landing-tooltip::before {
    top: -32px;
    content: " ";
    display: block;
    height: 20px;
    left: 40px;
    position: absolute;
    border-left: solid transparent 8px;
    border-right: solid transparent 8px;
    border-bottom: solid #8e8e8e 8px;
    border-width: 11px;
}

.landing-tooltip::after {
    border-left: solid transparent 10px;
    border-right: solid transparent 10px;
    border-bottom: solid #ffffff 10px;
    top: -30px;
    content: " ";
    height: 20px;
    left: 41px;
    position: absolute;
    width: 0;
  }

.landing-tooltip--icon {
    background-image: url(data:image/png;base64,);
    background-size: 15px 15px;
    width: 15px;
    height: 15px;
    margin-right: 10px;
    margin-top: 1px;
}
